	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>🌡24℃ 🌧</strong></p>\n<p>午前中は家族でホームセンターへと向かいました。\n鶴ヶ島バイパス沿いにあるビバホームです。</p>\n<p>昼もすぎたので、北坂戸駅近くのマクドナルドで昼食を取り帰宅しました。</p>\n<p>その後ファミコンをやってたらカレーの匂いがしたので、夕食がすぐ解りました。</p>","frontmatter":{"cover":null,"date":"1990-09-15T21:00:00+09:00","strDate":"1990/9/15","title":"日記"}}},"pathContext":{"path":"/ja/blog/1990/09/15-diary/","langKey":"ja"}}

